// Component CARD    
export const Card = () => {
    const [A, setA] = useState(array_card)
    const Afunc = (ts) => {
        const arrayfilter = A.filter(item => item !== ts)
        setA(arrayfilter)
    }

    // hàm reset 
    const reset = () => {
        setA(array_card)
    }

    // pagination
    const Pre = () => {
        const array1 = array_card.slice(0, 5);
        const array2 = array_card.slice(5, 10);
        if (A !== array_card && A !== array2) {
            setA(array1)
        }
    }
    // pagination
    const Next = () => {
        const array1 = array_card.slice(0, 5);
        const array2 = array_card.slice(5, 10);
        if (A !== array_card && A !== array1) {
            setA(array2)
        }
    }
      
    const FilterSP = (e) => {
        if (e.target.value === "1") {
            // console.log("one")
            const a = array_card.filter(item => item.price >= 300)
            setA(a)
        } else if (e.target.value === "2") {
            const a = array_card.filter(item => item.price < 300)
            setA(a)
        }
    }

    // hàm modal open + close 
    const [B, setB] = useState(" ")
    const [BB, setBB] = useState()
    const [C, setC] = useState()
    const [D, setD] = useState(" ")
    const [E, setE] = useState({ display: "none" })
    const [F, setF] = useState()

    const ModalOpen = e => {
        return (
            setB(e.target.src),
            setBB(e.target.dataset.text),
            setC(e.target.id),
            setD("modal fade show"),
            setE({ display: "block" }),
            setF(<div className='modal-backdrop fade show'></div>)
        )
    }
    const ModalClose = () => {
        return (
            setD("modal fade"),
            setE({ display: "none" }),
            setF()
        )
    }

   
    return (
        <>
            <div id="container1">
                {A.map(item => (
                    <div className="card" >
                        <img type="button" className="card-img-top" alt="none"
                            src={item.img} id={item.name} data-text={item.content} onClick={ModalOpen} />
                        <div className="card-body">
                            <h4 className="card-title" > {item.name} </h4>
                            <button className='btn btn-outline-danger' onClick={() => Afunc(item)}> xóa thẻ </button>
                            <h5 id='h5_card' className="card-title" > Price: {item.price} </h5>
                        </div>
                    </div>
                ))}

                <nav className='nav'>
                    <Pagination
                        click1={() => setA(array_card.slice(0, 5))}
                        click2={() => setA(array_card.slice(5, 10))}
                        clickPre={Pre}
                        clickNext={Next}
                    />
                </nav>
                <div id="row">
                    <button id='resetbutton' className='btn btn-danger' onClick={reset}>Reset all card</button>
                    <select className="form-control-sm mx-sm-3 mb-2" onChange={FilterSP}>
                        <option >Loc san pham</option>
                        <option value={"1"}>giá lớn hơn 300k</option>
                        <option value={"2"}>giá nhỏ hơn 300k</option>
                    </select>
                </div>
            </div>
            <Modal title={C} body={B} class={D} style={E} click={ModalClose} content={BB} />
            {F}
        </>
    )

}

//Element pagination
const Pagination = (ts) => {
    return (
        <ul className="pagination" >
            <li className="page-item"> <button id={ts.id} className="page-link" onClick={ts.clickPre}> Previous</button></li>
            <li className="page-item"> <button className="page-link" onClick={ts.click1}> 1</button></li>
            <li className="page-item"> <button className="page-link" onClick={ts.click2}> 2</button></li>
            <li className="page-item"> <button className="page-link" onClick={ts.clickNext}> Next</button></li>
        </ul>
    )
}

const Modal = (ts) => {
    return (
        <div className={ts.class} role="dialog" style={ts.style}>
            <div className="modal-dialog" role="document">
                <div className="modal-content animate__animated animate__fadeInDown" style={{ width: "500px" }}>
                    <div className="modal-header">
                        <h5 className="modal-title">{ts.title}</h5>
                        <button type="button" className="btn btn-light" onClick={ts.click}>
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        <img src={ts.body} alt="" />
                        <p>{ts.content}</p>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-light" onClick={ts.click}>Close</button>
                    </div>
                </div>
            </div>
        </div>
    )
}


